<template>
  <span style="overflow:unset;grid-column: 1 / -1">
    <ul v-if="cobDebug">
      <li v-for="(item,index) in value" :key="index">
        {{item}}
      </li>
    </ul>
    <div v-show="cobDebug" style="display:inline;vertical-align:middle"> <!-- vvv Debug info only vvv -->
      <v-tooltip bottom> 
        <template v-slot:activator="{ on }">
          <span v-on="on" class="active_alert" :style="myID==updater?'color:red':'color:blue'" > i</span>
        </template>
        <span>
          AgentID: {{myID}} <br>
          Updated by: {{updater}} <br>
          Expiration: {{updateTime}} <br>
          Def: {{def}} <br>
          Query: {{query}} 
          <span v-show="errors" style="color:#FBB"><br>{{errors}}</span>
        </span>
      </v-tooltip> 
    </div> <!-- ^^^ Debug info only ^^^ -->
    <slot :value="value"></slot>
  </span>
</template>

<script>
  import CCount from "./CCount";
  import axios from 'axios';

  export default {
    name: "c-records",
    mixins: [CCount],
    data () {
      return {
        size : 100
      }
    },
    methods: {
      getCacheId() {
        return "records-"+this.def + "-" + this.query;
      },
      getQueryUrl() {
        return "/recordm/recordm/definitions/search/name/"+this.def+"?from=0&size=500&q="+this.query;
      },
      getNewValue() {
        return axios.get(this.queryUrl).then(response => response.data.hits.hits.map(e => e._source))
      }
    }
  };
</script>