<template>
  <span>
    <a :href="resultsURL"> {{displayValue}} </a>
    <div v-show="cobDebug" style="display:inline;vertical-align:middle"> <!-- vvv Debug info only vvv -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <span v-on="on" class="active_alert" :style="myID==updater?'color:red':'color:blue'" > i</span>
        </template>
        <span>
          AgentID: {{myID}} <br>
          Updated by: {{updater}} <br>
          Expiration: {{updateTime}} <br>
          Product: {{product}} <br>
          Def: {{def}} <br>
          DomainId: {{domainId}} <br>
          Query: {{query}}
          <span v-show="errors" style="color:#FBB"><br>{{errors}}</span>
        </span>
      </v-tooltip>
    </div> <!-- ^^^ Debug info only ^^^ -->
  </span>
</template>

<script>
  import {cacheableMixin} from "../mixins/Cacheable.js";
  import {debuggableMixin} from "../mixins/Debuggable.js";
  import axios from 'axios';

  let confs = {
    recordm: {
      resultsURLTemplate: "/recordm/#/definitions/__DEF_ID__/q=__QUERY__",
      queryURLTemplate: "/recordm/recordm/definitions/search/name/__DEF_NAME__?from=0&size=0&q=__QUERY__",
      cacheIdTemplate: "count-__DEF_NAME__-__QUERY__",
      resultsURLTemplate_domain: "/recordm/#/domain/__DOMAIN_ID__/q=__QUERY__",
      queryURLTemplate_domain: "/recordm/recordm/domains/search/__DOMAIN_ID__?from=0&size=0&q=__QUERY__",
      cacheIdTemplate_domain: "count-domain-__DOMAIN_ID__-__QUERY__"
    },
    confm: {
      resultsURLTemplate:"/confm/#/search/q=__QUERY__",
      queryURLTemplate:"/confm/confm/search?from=0&size=0&q=__QUERY__",
      cacheIdTemplate:"count-confm-__QUERY__"
    },
    devicem:{
      resultsURLTemplate:"/devicem/#/search/q=__QUERY__",
      queryURLTemplate:"/devicem/devicem/search?from=0&size=0&q=__QUERY__",
      cacheIdTemplate:"count-devicem-__QUERY__"
    },
    workm:{
      resultsURLTemplate:"/workm/#/workm.search.tasks/q=__QUERY__",
      queryURLTemplate:"/workm/workm/search?from=0&size=0&q=__QUERY__",
      cacheIdTemplate:"count-workm-__QUERY__"
    }
  };


  export default {
    name: "c-count",
    mixins: [cacheableMixin,debuggableMixin],
    props: {
      product: {
        type:String,
        default: "recordm"
      },
      def: String,
      domainId: Number,
      query: {
        type: String,
        default: "*"
      },
      view: Number
    },
    data () {
      return {
        defId : Number,
        queryUrl: String,
        resultsURL: String
      }
    },
    created() {
      this.valueName = this.getCacheId(); //Cache necessita disto para ter uma chave única por pedido
      this.queryUrl = this.getQueryUrl();

      if ('recordm' === this.product.toLowerCase()) {
        axios.get("/recordm/recordm/definitions/name/" + this.def).then(response => {
          this.defId = response.data.id;
          this.updateResultsUrl()
        }).catch(e => {
          this.errors = e; //Debug info only
        });
      }
    },
    computed: {
      displayValue() {
        if(typeof this.value == "number") {
          return new Intl.NumberFormat('pt-BR', {}).format(this.value)
        } else if (typeof this.value == "string") {
          return this.value;
        } else {
          return "..." //Sinaliza cálculo
        }
      }
    },
    methods: {
      getCacheId() {
        let cacheId;
        if(this.domainId > 0){
          cacheId = confs[this.product.toLowerCase()].cacheIdTemplate_domain
          cacheId = cacheId.replace('__DOMAIN_ID__',this.domainId);
        } else{
          cacheId = confs[this.product.toLowerCase()].cacheIdTemplate
          cacheId = cacheId.replace('__DEF_NAME__',this.def);
        }
        cacheId = cacheId.replace('__QUERY__',this.query);

        return cacheId;
      },

      getQueryUrl() {
        let queryUrl;
        if(this.domainId > 0){
          queryUrl = confs[this.product.toLowerCase()].queryURLTemplate_domain;
          queryUrl = queryUrl.replace('__DOMAIN_ID__',this.domainId);
        } else{
          queryUrl = confs[this.product.toLowerCase()].queryURLTemplate;
          queryUrl = queryUrl.replace('__DEF_NAME__',this.def);
        }
        queryUrl = queryUrl.replace('__QUERY__',this.query);

        this.updateResultsUrl();  //Sempre que muda a query temos de actualizar o url tb
        return queryUrl;
      },

      updateResultsUrl() {
        if(this.domainId > 0){
          this.resultsURL = confs[this.product.toLowerCase()].resultsURLTemplate_domain
          this.resultsURL = this.resultsURL.replace('__DOMAIN_ID__',this.domainId);
        } else{
          this.resultsURL = confs[this.product.toLowerCase()].resultsURLTemplate
          this.resultsURL = this.resultsURL.replace('__DEF_ID__',this.defId);
          this.resultsURL += this.view ? "&av="+this.view : ""
        }
        this.resultsURL = this.resultsURL.replace('__QUERY__',this.query);
      },

      getNewValue() {
        return axios.get(this.queryUrl).then(response => response.data.hits.total)
      }
    }
  };
</script>