<template>
    <v-flex v-bind="$attrs">
      <div :class="{cobcomponent:true, board:true, flash:flash}" @dblclick="toggleDebug"> 
          <slot></slot>
      </div>
    </v-flex>
</template>

<script>
  import { debuggableMixin } from "../mixins/Debuggable.js";

  export default {
    name: 'c-board',
    mixins: [debuggableMixin],
    data () {
      return {
        flash: false
      }
    },
    created() {
      let me = this;
      this.$on('value-changed', me.signalChange );
    },
    methods: {
      signalChange() {
        let me = this;
        setTimeout(() => me.flash = true,300);
        setTimeout(() => me.flash = false,1300);
      }
    }
  }
</script>

<style>
  .cobcomponent.board {
      text-align: left;
      background-color: #fffcfc;
      border-style: solid;
      border-color: #BBB;
      border-width: 1px;

      display: grid;
      grid-template-columns: 20px auto auto;
      align-content: start;	

      padding: 5px;
      color:#474747;

      -webkit-transition: background-color 500ms linear;
      -moz-transition: background-color 500ms linear;
      -o-transition: background-color 500ms linear;
      -ms-transition: background-color 500ms linear;
      transition: background-color 500ms linear;
  }
  .cobcomponent.board.flash {
      background-color: #FDEAC0 !important;
      -webkit-transition: background-color 200ms linear;
      -moz-transition: background-color 200ms linear;
      -o-transition: background-color 200ms linear;
      -ms-transition: background-color 200ms linear;
      transition: background-color 200ms linear;
  }
</style>

