/* FEATURES */
// 0) A chave para a cache é o nome definido por quem inclui o Mixin
// 1) Na primeira inicialização mostra "..." para indicar que está a calcular
// 2) Se já há valor em cache passa a mostrar-lo antes de tudo mais
// 3) Se elemento não está visível não executa nenhum update (contra servidor ou qq outro)
// 4) Calcula o valor se não há um valor em cache, se a cache já expirou ou se expirar depois do tempo pretendido
// 5) Durante o desenvolvimento, enquanto não houver a função getNewValue no componente, utiliza um valor aleatório
// 6) Envio de eventos de valor alterado para elemento e pai
// 7) O valor guardado pode ser qualquer objecto javascript (apenas um número, ou string, ou um array de reultados com vários campos, por exemplo)
// 8) Mantêm informação de debug que pode depois ser usada na interface do componente para ajudar a avaliar problemas
//   8.1) tab+componente_vue da própria instância
//   8.2) tab+componente_vue da instância que actualizou a cache pela última vez
//   8.3) instante (por vezes estimado) da altura de actualização da cache
// 9) Quando arranca apaga todos os items que tenham mais de 5 dias 
// 10) TODO: se houver excepção a usar a localstorage podemos limpar a mesma e voltar a tentar. O limite normal são 5Mb total.

export const cacheableMixin = {
  props: {
    validity: {
      type: Number,
      default: 60
    }
  },
  data() {
    return {
      value: Object,
      valueName:String,
      updater:String, //Debug Info only
      myID: String,   //Debug Info only
      updateTime:Date //Debug Info only
    };
  },
  computed: {
    cacheId () {
      let loginUser = this.$root.$children[0].loginUser;
      return loginUser + "-" + this.valueName;
    }
  },
  mounted() {
    let tabID = sessionStorage.tabID ? sessionStorage.tabID : (sessionStorage.tabID = Math.random()); //Debug Info only
    this.myID = Math.floor(tabID * 1000) + "-" + this._uid; //Debug Info only
    this.updateValue();

    for (var i = 0, len = localStorage.length; i < len; ++i) {
      let key = localStorage.key(i);
      let now = Date.now()
      if (key && key.endsWith("_ExpirationTime")) {
        let expirationTime = localStorage.getItem(key);
        if (expirationTime < now - 5 * 24 * 60 * 60 * 1000) {
          let keyName = key.substr(0,key.indexOf("_ExpirationTime"));
          localStorage.removeItem(key);
          localStorage.removeItem(keyName + "_Value");
          localStorage.removeItem(keyName + "_Updater");
        }
      }
    }
  },
  watch: {
    query: function() { 
      this.valueName = this.getCacheId();
      this.queryUrl = this.getQueryUrl();
      this.forceRefresh(); 
    },
    value(newValue) {
      this.$emit("input",newValue);
    }
  },
  methods: {
    updateValue() {
      let now = Date.now();

      //Se não estiver visível retorna sem actualiza valor 
      //TODO: Levar em consideração casos em que quero os cálculos mesmo quando não está visível
      //this.$el.isConnected não está a funcionar correctamente no Edge. 
      if (!this.$el /*|| !this.$el.isConnected*/) { return }

      // Obtem valores da localStore
      var storedValue = localStorage.getItem(this.cacheId + "_Value");
      if (storedValue != null && storedValue !== 'undefined') {
        this.value = JSON.parse(storedValue); // Começa por usar a cache
      }
      this.updater = localStorage.getItem(this.cacheId + "_Updater") || 0; //Debug Info only
      let expirationTime = localStorage.getItem(this.cacheId + "_ExpirationTime") || 0; //Fazer isto imediatamente antes do teste à expiração para minimizar tempo de colisão
      
      //Se a cache está fora de validade ou o tempo que falta para expirar é maior que a validade, então obtem novo valor
      if ( now > expirationTime || expirationTime - now > this.validity*1000 || storedValue == null ) {
        localStorage.setItem(this.cacheId + "_ExpirationTime", now + this.validity*1000); //Fazer isto primeiro para minimizar tempo de colisão
        localStorage.setItem(this.cacheId + "_Updater", this.updater = this.myID); //Debug Info only

        this.getNewValue().then(value => {
          this.updateTime = new Date(now + this.validity*1000).toISOString(); //Debug Info only
          if(JSON.stringify(this.value) != JSON.stringify(value)) {
            this.value = value;
            //Partimos do princípio que os componentes com este mixin só se preocupam com o valor. A apresentação é feita pelo pai
            this.$emit("value-changed");
            this.$parent.$emit("value-changed"); 
          } 
        }).catch(e => {
          this.errors = e; //Debug info only
        }).finally( () => {
          if (typeof this.value !== 'undefined' && typeof this.value !== 'function') {
            localStorage.setItem(this.cacheId + "_Value", JSON.stringify(this.value))
          }
        })
      } else {
        this.updateTime = new Date(expirationTime*1).toISOString() //Debug Info only - estimated value
      }
    },
    forceRefresh() {
      localStorage && localStorage.setItem(this.cacheId + "_ExpirationTime", 0);
      this.updateValue();
    },
    getNewValue() {
      // Isto tem de ser definido por quem inclui este mixin.
      // Esta definição é só um exemplo que pode ser usado durante o desenvolvimento
      // O resultado tem sempre de ser uma promise
      return new Promise((resolve) => {
        resolve(Math.floor(Math.random() * 100))
      })
    },
    waitFor(variableName) {
      // Helper para ajudar as funções getNewValues a esperar pelos argumentos antes de executarem os pedidos
      let variable = eval(variableName);
      return new Promise(function (resolve) {
          if (typeof variable === "function" || typeof variable === "string" && variable.indexOf("native code")>0) { //TODO: O método e o teste não estão perfeitos mas parecem funcionar bem
            setTimeout( () => this.waitFor(variableName), 200); 
            // eslint-disable-next-line no-console
            //console.warn("Ups, trying later...")
          } else {
            return resolve();
          }
      }.bind(this))
    }
  }
};
