<template>
  <span>
    <a :href="resultsURL"> {{displayValue}} </a>
    <div v-show="cobDebug" style="display:inline;vertical-align:middle"> <!-- vvv Debug info only vvv -->
      <v-tooltip bottom> 
        <template v-slot:activator="{ on }">
          <span v-on="on" class="active_alert" :style="myID==updater?'color:red':'color:blue'" > i</span>
        </template>
        <span>
          AgentID: {{myID}} <br>
          Updated by: {{updater}} <br>
          Expiration: {{updateTime}} <br>
          Query: {{query}} 
          <span v-show="errors" style="color:#FBB"><br>{{errors}}</span>
        </span>
      </v-tooltip> 
    </div> <!-- ^^^ Debug info only ^^^ -->
  </span>
</template>

<script>
  import CEs from "./CEs";
  import axios from 'axios';

  export default {
    name: "c-sum",
    mixins: [CEs],
    props: {
      field: String
    },
    data () {
      return {
        resultsURL: String
      }
    },
    created () {
      this.resultsURL =  "#/definitions/"+this.index.substring(this.index.indexOf("-")+1)+"/q=" + this.query;
    },
    computed: {
      displayValue() {
        if(typeof this.value == "number") {
          return new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'EUR' } ).format(this.value)
        } else if (typeof this.value == "string") {
          return this.value;
        } else {
          return "..." //Sinaliza cálculo
        }
      }
    },    
    methods: {
      getCacheId() {
        return "sum-"+this.index + "-" + this.query + this.field;
      },
      getNewValue() {
        let data = {
                      "query": {
                        "bool": {
                          "must": [
                            {
                              "query_string": {
                                "query": this.query,
                                "analyze_wildcard": true
                              }
                            }
                          ],
                          "must_not": []
                        }
                      },
                      "aggs": {
                        "soma": {
                          "sum": {
                            "field": this.field
                          }
                        }
                      }
                    } 
        return this.waitFor("this.queryUrl")
          .then( () => axios.post(this.queryUrl, data)
          .then(response => response.data.aggregations.soma.value) 
        )
      }    
    }
  };
</script>