<script>
  import CEs from "./CEs";
  import axios from 'axios';

  export default {
    name: "c-distincts",
    mixins: [CEs],
    props: {
      field: String
    },
    methods: {
      getCacheId() {
        return "distincts-"+this.index + "-" + this.query + this.field;
      },
      getNewValue() {
        let data = {
                    "query": {
                      "query_string": {
                        "query": this.query,
                        "analyze_wildcard": true
                      }
                    },
                    "aggs": {
                      "distinct": {
                        "terms": {
                          "field": this.field,
                          "size": 50
                        }
                      }
                    }
                  }
        return this.waitFor("this.queryUrl")
          .then( () => axios.post(this.queryUrl, data))
          .then(response => response.data.aggregations.distinct.buckets.map(e => e.key))
      }
    }
  };
</script>