<template>
  <span style="overflow:unset;grid-column: 1 / -1">
    <ul v-show="cobDebug">
      <li v-for="(item,index) in value" :key="index">
        {{item}}
      </li>
    </ul>
    <div v-show="cobDebug"> <!-- vvv Debug info only vvv -->
      <v-tooltip bottom> 
        <template v-slot:activator="{ on }">
          <span v-on="on" class="active_alert" :style="myID==updater?'color:red':'color:blue'" > i</span>
        </template>
        <span>
          AgentID: {{myID}} <br>
          Updated by: {{updater}} <br>
          Expiration: {{updateTime}} <br>
          Index: {{index}}
          <span v-show="errors" style="color:#FBB"><br>{{errors}}</span>
        </span>
      </v-tooltip> 
    </div> <!-- ^^^ Debug info only ^^^ -->
    <slot :value="value"></slot>
  </span>
</template>

<script>
  import { cacheableMixin } from "../mixins/Cacheable.js";
  import { debuggableMixin } from "../mixins/Debuggable.js";
  import axios from 'axios';

  export default {
    name: "c-es",
    mixins: [cacheableMixin,debuggableMixin],
    props: {
      index: String,
      query: {
        type: String,
        default: "*"
      }
    },
    data () {
      return {
        queryUrl: String
      }
    },
    created () {
      this.valueName = this.getCacheId(); //Cache necessita disto para ter uma chave única por pedido
      this.queryUrl = this.getQueryUrl();
    },
    computed: {
      displayValue() {
        if(typeof this.value == "object") {
          return JSON.stringify(this.value)
        } else {
          return "..." //Sinaliza cálculo
        }
      }
    },
    methods: {
      getCacheId() {
        return "es-"+this.index + "-" + this.query;
      },
      getQueryUrl() {
        if(this.index && this.index.includes("*")){
          return "/es/"+this.index+"/_search?from=0&size=0";
        }
        
        return "/recordm/recordm/definitions/search/advanced/"+this.index
      },
      getNewValue() {
        let data = {
                    "query": {
                      "query_string": {
                        "query": this.query,
                        "analyze_wildcard": true
                      }
                    },
                    "aggs": {
                        "definicoes": {
                            "terms": {
                                "field": "definitionName.raw"
                            }
                        }
                    }
                  }
        return this.waitFor("this.queryUrl")
          .then( () => axios.post(this.queryUrl, data))
          .then(response => response.data.aggregations.definicoes.buckets)
      }
    }
  };
</script>