export const debuggableMixin = {
    data() {
        return {
            cobDebug: false,
            errors:""
        };
    },
    methods: {
        recursiveToggleDebug(me) {
            let recursiveToggleDebug = this.recursiveToggleDebug;
            if (me.cobDebug !== undefined) me.cobDebug = !me.cobDebug;
            me.$children.forEach(c => recursiveToggleDebug(c));
        },
        toggleDebug(evt) {
            if (evt.altKey) {
                this.recursiveToggleDebug(this);
            } 
          }
    }
};