<template>
  <transition name="fade">
    <span class="cob-cell" v-if="show"
      :class="{
        center_Header:   header, 
        center_Line:     line, 
        action_Add:      add, 
        left_Title:      title, 
        left_Subtitle:   subtitle, 
        right_Plain:     plain, 
        right_Info:      info, 
        right_Success:   success, 
        right_Warning:   warning, 
        right_Important: important,
        right_Success1:  success1, 
        right_Success2:  success2, 
        right_Success3:  success3, 
        right_Success4:  success4, 
        right_Success5:  success5
      }">
      <slot></slot>
    </span>
  </transition>
</template>

<script>
  export default { 
    name: 'c',
    props: {
      header: Boolean,
      line: Boolean,
      add: Boolean,
      title: Boolean,
      subtitle: Boolean,
      plain:Boolean,
      info:Boolean,
      success: Boolean,
      success1: Boolean,
      success2: Boolean,
      success3: Boolean,
      success4: Boolean,
      success5: Boolean,
      warning: Boolean,
      important: Boolean
    },
    data () {
      return {
        show: true
      }
    },
    created() {
      let me = this;
      this.$on('value-changed', me.signalChange );
    },
    methods: {
      signalChange() {
        let me = this;
        setTimeout(() => me.show = false,10);
        setTimeout(() => me.show = true,1000);
        setTimeout(() => me.show = false,2000);
        setTimeout(() => me.show = true,3000);
        setTimeout(() => me.show = false,4000);
        setTimeout(() => me.show = true,5000);
        //Dar opção ao board para também reagir a alterações
        this.$parent.$emit("value-changed");       }
    }
  }
</script>

